import address from './address';
import {mapState} from 'vuex';
export default{
	name:'navbar',
	components:{
		address
	},
	props:["bgColor", "fgColor", "showAddress", "title", "back", "isHome"],
	data:function(){
		return {
			paddingTop:uni.getSystemInfoSync().statusBarHeight,
			backgroundColor:'#fff',
			foregroudColor:'#000'
		}
	},
	computed:{
		...mapState(["appName", "currentAddress"]),
		background(){
			return this.bgColor? this.bgColor:this.backgroundColor
		},
		color(){
			return this.fgColor? this.fgColor:this.foregroudColor
		},
		dispTitle(){
			return this.title? this.title:this.appName
		},
		addressColor(){
			return this.isHome ? "#fff" : "#585858"
		}
	},
	methods:{
		switchAddress(){
			uni.navigateTo({
				url:'/pages/address/addressList',
				success:res=>{
					console.log(res)
				},
				fail:res=>{
					console.log(res)
				}
			})
		},
		navigateBack(){
			uni.navigateBack({
				
			})
		}
	}
}
